import wButton from '@/components/watch-login/watch-button.vue' //button
import wPicker from "@/components/w-picker/w-picker.vue";
export default {
	components:{
		wButton,
		wPicker
	},
	data() {
		return {
			windowHeight:0,//屏幕高度
			phone:'',//手机号
			password:'',//密码
			nickname:'',//姓名
			idnum:'',//身份证号
			men:-1,//0男？1女
			isRotate: false, //是否加载旋转
			selectList:[{
				label:"男",
				value:0
			},{
				label:"女",
				value:1
			}],
			sex:'',//男女
		}
	},
	onLoad() {
		var that = this
		uni.getSystemInfo({
		    success: function (res) {
				that.windowHeight = res.windowHeight
		        console.log('屏幕高度为'+res.windowHeight);
		    }
		});
	},
	methods: {
		//注册
		startLogin(){
			var that = this
			// 防抖动
			if(that.isRotate) return
			if(!(/^1[3456789]\d{9}$/.test(that.phone))){
				uni.showToast({
					title:'请输入正确的手机号',
					icon:'none'
				})
				return
			}
			console.log(that.password.length)
			if(that.password.length < 6){
				uni.showToast({
					title:'密码最少为6位',
					icon:'none'
				})
				return
			}
			var regName =/^[\u4e00-\u9fa5]{2,4}$/;  
			if(!regName.test(that.nickname)){
				uni.showToast({
					title:'请输入真实姓名',
					icon:'none'
				})
				return
			}
			if(that.men < 0){
				uni.showToast({
					title:'请选择性别',
					icon:'none'
				})
				return
			}
			let reg = /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/;
			if(!(reg.test(that.idnum))){  
				uni.showToast({
					title:'请输入正确的身份证号',
					icon:'none'
				})
			    return 
			} 
			that.isRotate = true
			
			setTimeout(function(){
				that.isRotate = false
			},3000)
			
			let data = {
				phone:that.phone,
				user_pwd:that.password,
				user_name:that.nickname,
				card_id:that.idnum,
				sex:that.men
			}
			
		},
		//男女选择开关
		shopTypeClick(){
			this.$refs.picker.show();
		},	
		//选择男女
		onConfirm(val){
			console.log(JSON.stringify(val))
			this.sex= val.result
			this.men = val.checkArr.value
			console.log(this.men)
		},
		
		
	}
}
